@import '../../styles/config.scss';

.header-title-block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  text-align: center;

  .title-container {
    width: 80%;
    height: 80%;
    color: $gold-color;
    text-shadow: 0.1rem 0.1rem $main-color;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(128, 128, 128, 0.3);
    border: 0.05rem solid $base-color;
    outline:0.1rem solid $base-color;
    outline-offset: 1rem;

    h2 {
      font-size: 1.8rem;

      @media screen and (min-width: 31rem) {
        font-size: 3rem;
      }
    }
  }
}
